.flex{
    display: flex;
    background-color: transparent;
    border: none;
   
    
}

.logo{
    height:52px;
    width: 190px;
    border-radius: 50px;
    color: rgb(63, 77, 120);
    background: transparent;
    padding-left: 21px;
    padding-top: 10px;
    margin-left: 30px;
    margin-top: 7px;
    font-weight: 700;
   
    
}

.logo-styling{
    margin-left: 30px;
}

.logo-img{
    margin-top: 3px;
    height: 55px;
    width: 52px;
}

.logo-name{
    margin-top: 20px;
}

.NavBar{
    display: flex;
    background-color: rgb(27, 124, 124);
    height:65px;

}

.Nav-logo{
    height:65px;
    width: 130px;
    border-radius: 50px;

}

.flex{
    display: flex;
}

.Nav-Right{
    display: flex;
    height:65px;
    width: 630px;
}

.Nav-Left{
    
    margin-left: auto;
    margin-right: 0;
}

.btn-SUB{
    margin-top:10px;
    border-radius: 50px;
    margin-left:15px;
    margin-right: 15px;
    height: 45px;
    width: 100px;
    box-shadow: 1px 1px 1px 1px rgb(212, 211, 211);
}

.btn-SUB:hover{
    background-color: rgb(0, 189, 196);
    color:#ffffff;
    
}

.btn-EXPLORE{
   
    background-image: linear-gradient(to right, #ce31fd 0%, #514A9D  51%, #24C6DC  100%)
    
}

.btn-EXPLORE {
    
    margin-right: 450px;
    margin-top:10px;
    margin-left:50px;
    padding: 5px 25px;
    text-align: center;
    text-transform: uppercase;
    transition: 0.5s;
    background-size: 200% auto;
    color: white;            
    border-radius: 10px;
    border:none;
    border-radius: 50px;
    height: 45px;
    box-shadow: 1px 1px 1px 1px rgb(212, 211, 211);
    
  }

.btn-EXPLORE:hover {
    background-position: right center; /* change the direction of the change here */
    color: #fff;
    text-decoration: none;
  }

.Nav-Middle{
    height:50px;
    width: 600px;
}

.btn-nav{
  
    color:rgb(0, 0, 0);;
    font-weight: 500;
    font-size: large;
    border-radius: 35px;
    width:80px;
    height: 45px;
    margin: 10px;
    border: none;
}



.form-control{
    border-radius: 35px;
    height: 45px;
    margin: 10px;
}

.form-inline{
    margin-right: 120px;
    margin-left: 120px;

}


.btn-outline-success {background-image: linear-gradient(to right, #ce31fd 0%, #514A9D  51%, #24C6DC  100%)}
.btn-outline-success {
   margin: 10px;
   padding: 5px 25px;
   text-align: center;
   text-transform: uppercase;
   transition: 0.5s;
   background-size: 200% auto;
   color: white;            
   border-radius: 10px;
   display: block;
   border:none;
   border-radius: 50px;
   height: 45px;
   text-decoration: none;
 }

 .btn-outline-success:hover {
   background-position: right center; /* change the direction of the change here */
   color: #fff;
   text-decoration: none;
 }

 
