.search-panel{
    display: flex;
    
}

.search-panel-align{
    
    margin-left: auto;
    margin-right: auto;
    
}

.search-bar{
    width:300px;
    margin:10px;
}

.search-label{
    color:rgb(147, 148, 150);
}

.input-field{
    width: 80%;
  padding: 12px 20px;
  
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 3px;
  box-sizing: border-box;
  outline-color:rgb(0, 140, 255);
  height: 38px;
}

.btn-SUBMIT{
    margin-top:30px;
    border-radius: 50px;
    margin-left:15px;
    margin-right: 15px;
    height: 45px;
    width: 100px;
    background: rgb(7, 213, 250);
    box-shadow: 1px 1px 1px 1px rgb(212, 211, 211);
}

