.list-panel{
    margin: auto;
    width: 50%;
    margin-top: 50px;
    padding: 10px;
    border:none;
    border-radius: 10px;
    
}

.main-card{
    display:flex;
    margin:10px;
    border-color: tomato;
    border-width: thick;
}

.sub-card{
    width: 100%;
}


.float-layout {
    padding: 5px 5px;
    float: left;
    width: 100%;
    height: auto;
    box-sizing: border-box;
    margin: 5px;
  }
  
  
  .card-container {
    overflow: hidden;
  }
  
  .card {
    background-color: transparent;
    color: black;
    min-height: 100%; /*replace this it in width: 100%*/
    width: 50%;
    
  }
  
  .card-title {
    font-size: 30px;
    text-align: left;
    font-weight:normal;
    padding-top: 20px;
  }
  
  .card-desc {
    padding: 10px;
    text-align: left;
    font-size: 18px;
    padding: 10px;
    height: 200px;
    
  }
  
  /*add this it*/
  .card-image {
    display: flex;
  }
  /*-------------*/
  
  div.card-image img {
    width: 50%;
    height: auto;
  }
  
  /* Phone Devices Query */
  @media only screen and (max-width: 37.5em) {
    div.card-image img {
      width: 100%;
      height: auto;
    }
    
    /*add this it*/
    .card-image {
       flex-direction: column;
    }
    /*----------------------*/
  
    .card-desc {
      width: 800px;
      margin-top: -4px;
      border-top-left-radius: 0px;
      border-bottom-left-radius: 0px;
    }
  }

  .text {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 3; /* number of lines to show */
            line-clamp: 3; 
    -webkit-box-orient: vertical;
 }

 .image-card{
    box-shadow: 0 0 5px rgba(0,0,0,0.8);
    clip-path: inset(-5px 0px -5px -5px);
    width:200px;
    height:200px;
 }

 .btn-icon{
     
    float:right;
    background:transparent;
    color: rgb(194, 192, 192);
    margin:10px;
    margin-top:-30px;
     
 }

 .delete-button{
     background-color:rgb(110, 148, 252);
     border:none;
     color: rgb(255, 255, 255);
     height: 100%;

 }

 .Paginate-bar{
    margin: auto;
    width: 50%;
    border: 3px solid green;
    padding: 10px;
    display: flex;
 }

 .paginate-btn{
     width:40px;
     height: 40px;
     background-color: rgb(9, 136, 255);
     border-radius:50px;
     margin:10px;
     float: right;
 }

