.flex-panel{
  display: flex;
  background-color: transparent;
  border: none;
  flex-wrap: wrap;
  height: fit-content;
 
  
}

.jumbotron-panel{
    
    
  margin-top: 30px;
  margin-left: 60px;
  border-radius: 10px;
  margin-left: auto;
  margin-right: auto;
  width:1150px;
  background: rgb(5,159,255);
  background: linear-gradient(90deg, #0d84c9 0%, #10b9ec  51%,   #07daff 100%);
  box-shadow: 5px 5px 5px rgb(151, 151, 151);

}




.btn-search {background-image: linear-gradient(to right, #ce31fd 0%, #514A9D  51%, #24C6DC  100%)}
.btn-search {
   margin: 19px;
   padding: 5px 25px;
   text-align: center;
   text-transform: uppercase;
   transition: 0.5s;
   background-size: 200% auto;
   color: white;            
   border-radius: 10px;
   display: block;
   border:none;
   border-radius: 50px;
   height: 45px;
 }

 .btn-search:hover {
   background-position: right center; /* change the direction of the change here */
   color: #fff;
   text-decoration: none;
 }

