.jumbotron{

    display: flex;
    width:1000px;
    background-color:transparent;
    margin-right: auto;
    margin-left: auto;
    height: fit-content;
    margin-top: 30px;
    border-radius: 30px;
    box-shadow: 1px 2px 3px 5px rgb(238, 236, 236);
   
}

.home-panel-img{
    width:450px;
    height: 300px;
    border-color:darkblue;
    margin-right: 0;
    margin:30px;
}

.home-panel-desc{
    margin:30px;
    width:450px;
    height: 300px;
    background-color:rgb(239, 205, 248);
    padding-top: 30px;
    border-radius: 25px;
}

.text-main{
    font-size: xx-large;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: lighter;
    text-align: center;
}