.photo-list{
    width:90%;
    list-style: none;
    margin:20px auto;
}


.photo-item{
    display:flex;
    width:300px;
    box-shadow: 0 5px 10px rbga(0,0,0,1.2);
    margin:20px 0;
}


.photo-item img{
    display : flex;
    width:100%;
    border:4px double rbga(0,0,0,1.2)
}


.masonry-image{
    border-radius: 20px;
}