
  
  
  .carousel-label{
    font-size: xx-large;
    margin-left: 70px;
    font-weight: lighter;
    text-decoration: underline;
    text-decoration-thickness: auto;
    text-decoration-thickness: from-font;
    
  }
  
  
  .carousel-wrapper {
    width: 1150px;
    margin-top: 50px;
    margin-left: 70px;
   
    margin-left: auto;
    margin-right: auto;
    display: block;
  
    
  }
  
  .rec-carousel-item:focus {
    
    background-color: aqua;
  }

  .rec.rec-arrow {
    justify-content: center;
    
    border: none;
    border-color: rgb(173, 11, 11);
    color: rgb(255, 255, 255);
    width: 70px;
    height: 70px;
    outline: none;
    padding-bottom: 3px;
    
}

.rec.rec-arrow:hover {
  justify-content: center;
  
  border: none;
  border-color: transparent;
  color: rgb(196, 23, 23);
  width: 70px;
  height: 80px;
  outline: none;
  
}

.rec.rec-arrow:disabled {
  visibility: hidden;
 
}

.rec-carousel-item:focus {
  outline: none;
  box-shadow: inset 0 0 1px 0px rgb(22, 189, 119);
  

}


.rec.rec-arrow-left:hover:enabled,
.rec.rec-arrow-right:enabled{

  

  background: rgb(5,159,255);
  background: linear-gradient(90deg, #ce31fd 0%, #b316f1  51%,   #6e63e0 100%);
 
    box-shadow: 0 0 1px 3px var(--clr-primary-400);
}

.rec.rec-arrow-right:hover:enabled{
  background: rgb(5,159,255);
  background: linear-gradient(90deg, #0d84c9 0%, #10b9ec  51%,   #07daff 100%);
    box-shadow: 0 0 1px 3px var(--clr-primary-400);
}

.rec.rec-arrow-left:enabled{

  background: rgb(5,159,255);
  background: linear-gradient(90deg, #0d84c9 0%, #10b9ec  51%,   #07daff 100%);

 
    box-shadow: 0 0 1px 3px var(--clr-primary-400);
}

.rec.rec-arrow-left:hover:enabled{
  background: rgb(5,159,255);
  background: linear-gradient(90deg, #ce31fd 0%, #b316f1  51%,   #6e63e0 100%);
    box-shadow: 0 0 1px 3px var(--clr-primary-400);
}



.rec-slider-container {
  
  
  height: 600px ;
 
}

.rec-dot{
  margin-top: 10px;
  background-color: rgb(97, 225, 65);
}
